.generate-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: linear-gradient(rgba(255, 255, 255, 0.7),
      rgba(255, 255, 255, 0.3));
  /* url("https://images.pexels.com/photos/114979/pexels-photo-114979.jpeg?auto=compress&cs=tinysrgb&dpr=2&w=500"); */
  background-size: cover;
  background-position: center;
}

.generate-form {
  background-color: white;
  padding: 0px 60px;
  border-radius: 10px;
}

.generate-h1 {
  color: #171717;
  text-align: center;
}

.generate-button {
  width: 100%;
  height: 50px;
  padding: 10px;
  border: none;
  background-color: #171717;
  color: white;
  border-radius: 5px;
  font-weight: bold;
  font-size: 18px;
  cursor: pointer;
  margin-top: 15px;
  margin-bottom: 30px;
}

button:hover {
  background-color: #01bf71;
}
.generateInput {
  display: flex;
  flex-direction: column;
  width: 280px;
}

.generate-input {
  width: 500px;
  padding-left: 5px;
  padding: 15px;
  margin: 10px 0px;
  border-radius: 5px;
  border: 1px solid gray;
}

.generate-label {
  font-size: 12px;
  color: gray;
}

.generate-span {
  font-size: 12px;
  padding: 3px;
  color: red;
  display: none;
}

.generate-input :invalid[focused="true"] {
  border: 1px solid red;
}

.generate-input :invalid[focused="true"] ~ span {
  display: block;
}

